<template>
<div>

  <section id="login_whole">
    <div id="member_banner"></div>
    <div id="breadcrumb_one">
      <a href="index.html">home</a>
      <a href="register.html"></a>
    </div>
    <div id="register">
      <h3></h3>
      <section id="small">
        <div id="r_small">
          <div>
            <label for='email'>EMAIL :</label>
            <input type="text" v-model="email" />
            {{emailmsg}}
          </div>
          <div>
            <label for='password'>password :</label>
            <input type="password" v-model="password" />
          </div>
          <div>
            <label for='name'>Name :</label>
            <input type="text" v-model="name" />
          </div>

          <button v-on:click="check"></button>
          <button v-on:click="join">join</button>


        </div>
      </section>
    </div>
  </section>

</div>
</template>



<script>
export default {

  name: 'Register',
  data() {
    return {
      email: '',
      password: '',
      name: '',
      //       
      demailchk: false,
      emailchk: false,
      pwdchk: false,
      emailmsg: 'aa'
    };
  },

  methods: {

    join: function() {
      const self = this;

      //   
      if (this.email == '' || this.password == '' || this.name == '') {
        alert('  ');
        return;
      }

      // ,   
      var e = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
      var p = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/;

      if (e.test(this.email)) {
        this.emailchk = true;
      } else {
        alert(" ");
        this.emailchk = false;
        return;
      }

      if (p.test(this.password)) {
        this.pwdchk = true;
      } else {
        alert(" 8 +");
        this.pwdchk = false;
        return;
      }

      if (!this.demailchk){
        alert(" ");
         return;
      }


      //   
      const form = new URLSearchParams();
      form.append('email', this.email);
      form.append('password', this.password);
      form.append('name', this.name);

      this.$axios.post('/members/register', form)
        .then(function(resource) {
          if (resource.data.result) {
            alert('');
            self.$router.push('/login');
          }
        });

    }, // join:

    check: function() {
      //  
      const self = this;
      const form = new URLSearchParams();
      if (this.email == '') {
        alert(' ');
        return;
      }
      form.append('email', this.email);
      self.$axios.post('/members/check', form)
        .then(function(resource) {
          if (resource.data.result) {
            self.emailmsg = resource.data.message;
            if (resource.data.available) {
              self.demailchk = true;
            }
          }
        }); // POST

      // , ,  6~24
      self.pwdchk = true;
    }

  }

}
</script>
