<template>
<div>
  <main>
    <section id="notice_whole">
      <div id="notice_banner"></div>
      <div id="breadcrumb_one">
        <a href="index.html">home</a>
        <a href=""></a>
      </div>
      <div id="notice">
        <h3 class="notice"></h3>
        <section id="w_s">
          <p></p>
          <textarea v-model="title" id="w_t" placeholder=" ."></textarea>
          <!--  html   -->
          <textarea v-model="content" id="w_c" placeholder=" ."></textarea>
          
          <button v-on:click="write" type="button" name="button" class="n_save"></button>
        </section>
      </div>
    </section>
  </main>

</div>
</template>


<script>
export default{
  name:'NoWrite',
  data(){
    return{
      title:'',
      content:''
    };
  },
  methods:{
    write: function(){
      const self = this;
      // check blank
      if(self.title=='' || self.content==''){
        alert('');
        return;
      }

      // write
      const form = new URLSearchParams();
      form.append('infoTitle', self.title);
      form.append('infoContent', self.content);
      self.$axios.defaults.headers.common['Authorization'] = self.$cookies.get('token');
      self.$axios.post('/notice/write', form)
      .then(function(resource){
        if(resource.data.result){
          alert(' ');
          self.$router.push('/no-list');
        }
      }); // POST
    } // write:
  }
}
</script>


