<template>
<div>
  <main>
    <section id="login_whole">
      <div id="member_banner"></div>
      <div id="breadcrumb_one">
        <a href="index.html">home</a>
        <a href="login.html"></a>
      </div>

      <div id="login">
        <h3></h3>
        <section id="small">
          <div id="s_small">
            <div id="id">
              <label for='email'></label>
              <input type="text" id='email' v-model='email' />
            </div>
            <div>
              <label for='password'></label>
              <input type="password" id='password' v-model='password' />
            </div>
            <button v-on:click='login'>login</button>
          </div>
          <div id="s_add">
            <p>/ ?<a href="">/ </a></p>
            <p>  ?<router-link to ='/register'></router-link></p>
          </div>
        </section>
      </div>
    </section>
  </main>
</div>
</template>


<script>
// index.js router   cookies  

export default {
  name: 'Login',
  data() {
    return {
      email: '',
      password: ''
    };
  },
  created:function(){
    const self = this;
    if (self.$cookies.isKey('token')){
      self.$router.push('/');
    }
  },
  methods: {
    login: function() {
      const self = this;
      const form = new URLSearchParams();
      form.append('email', self.email);
      form.append('password', self.password);

      this.$axios.post('/members/login', form)
        .then(function(resource) {
    //      alert(resource.data.result);
          if (resource.data.result) {
            // set token
            var mytoken = resource.headers.authorization; //  
            var mynum = resource.data.memnum;
    //        alert('mytoken:'+mytoken);
            // set token in cookie
            self.$cookies.set('token', mytoken, 60*60*2);
            self.$cookies.set('memnum', mynum, 60*60*2);
        //    alert('cookie:'+self.$cookies.get('token'));
            // set header with token
            self.$axios.defaults.headers.common['Authorization'] = self.$cookies.get('token');
            self.$router.go();
          } else {
            alert('login failed');
          }
        }); // POST
    } // login:

  } // methods:
}
</script>
